.policy {
	padding-top: calc(var(--header-height) + 1.5rem);
	padding-bottom: var(--header-padding);

	&-title,
	h3 {
		font-weight: 300;
		letter-spacing: normal;
		text-transform: uppercase;
	}

	&-title {
		text-align: center;
		margin-bottom: 1.5rem;

		@media (min-width: 992px) {
			margin-bottom: 2.5rem;
		}
	}

	&-content {
		max-width: 50rem;
		max-height: calc(100vh - var(--header-height) - var(--header-padding) - 8rem);
		max-height: calc(100svh - var(--header-height) - var(--header-padding) - 8rem);
		max-height: calc(100dvh - var(--header-height) - var(--header-padding) - 8rem);
		margin-inline: auto;
		padding-right: 1rem;
		font-size: 1rem;
		overflow-y: auto;
		overflow-x: hidden;

		@media (min-width: 992px) {
			max-height: calc(100vh - var(--header-height) - var(--header-padding) - 12rem);
			max-height: calc(100svh - var(--header-height) - var(--header-padding) - 12rem);
			max-height: calc(100dvh - var(--header-height) - var(--header-padding) - 12rem);
		}
	}

	h3 {
		font-size: inherit;
		margin-bottom: .25rem;

		&:not(:first-child) {
			margin-top: 1.5rem;
		}
	}

	p {
		margin-top: .25rem;
		text-align: justify;
	}

	.hero-footer {
		position: absolute;
		right: max(var(--container-offset), calc((100% - var(--content-width)) / 2));
		bottom: var(--header-padding);
		z-index: 1;

		&,
		&-copy {
			text-align: right;
		}
	}
}