.order-info {
	display: grid;
	justify-content: center;
	grid-template-columns: minmax(0, 1fr);
	gap: .25rem 1.25rem;
	font-size: .875rem;
	text-transform: uppercase;
	margin-bottom: 3rem;

	@media (min-width: 576px) {
		grid-template-columns: repeat(3, max-content);
	}

	@media (min-width: 992px) {
		margin-bottom: 7rem;
	}

	&-title {
		grid-column: -1 / 1;
		font-size: 1.25rem;
	}
}