.cart {
	max-width: 768px;
	color: var(--gray);
	margin-inline: auto;
	padding-inline: var(--container-offset);
	padding-block: calc(var(--header-height) + 2.5rem) 2.5rem;
	
	@media (min-width: 992px) {
		padding-block: var(--header-height) 7.5rem;
	}

	&-header {
		display: flex;
		flex-direction: row-reverse;
		justify-content: space-between;
		gap: 1.25rem;
		text-transform: uppercase;

		@media (min-width: 576px) {
			flex-direction: row;
			gap: 1.5rem;
		}
		
		@media (min-width: 992px) {
			gap: 4rem;
		}

		&:not(:last-child) {
			margin-bottom: 2.5rem;
			
			@media (min-width: 992px) {
				margin-bottom: 5rem;
			}
		}

		&-picture {
			max-width: 5rem;
			display: block;
			flex-shrink: 0;

			@media (min-width: 576px) {
				max-width: initial;
			}
		}

		&-image {
			display: block;
			border: 1px solid var(--gray);
			object-fit: contain;

			@media (min-width: 576px) {
				height: 100%;
				object-fit: cover;
			}
		}

		&-detail {
			display: flex;
			flex-direction: column;
			gap: 1rem;
			justify-content: space-between;
			flex: 1;

			@media (min-width: 576px) {
				flex-direction: row;
			}

			h3,
			h4 {
				letter-spacing: normal;
			}

			h3 {
				font-size: 1.25rem;
				
				@media (min-width: 992px) {
					font-size: 1.5rem;
				}
			}

			.shop-card-color {
				margin-left: .75rem;
				
				@media (min-width: 992px) {
					margin-left: 1rem;
				}
			}
		}

		&-parameters,
		&-prices {
			display: flex;
			flex-direction: column;
			gap: .25rem;

			> li {
				min-height: 1.25em;
				display: flex;
				align-items: center;
			}
		}

		&-prices {
			
			@media (min-width: 576px) {
				text-align: end;
			}

			> li {
				@media (min-width: 576px) {
					justify-content: flex-end;
				}
			}
		}
	}
}