.product {
	min-height: 100vh;
	min-height: 100svh;
	min-height: 100dvh;
	display: flex;
	flex-direction: column;

	@media (min-width: 768px) {
		flex-direction: row;
	}

	&-start,
	&-end {

		@media (min-width: 768px) {
			flex-basis: 50%;
		}
	}

	&-start {
		position: relative;

		.main-swiper-slide-hint {
			position: absolute;
			bottom: 1.5rem;
			left: 50%;
			transform: translateX(-50%);
			z-index: 1;
			
			@media (min-width: 768px) {
				width: 50%;
				color: var(--gray-800);
				position: fixed;
				left: 0;
				bottom: 2.5rem;
				transform: initial;
			}
		}
	}

	&-end {
		position: relative;
		padding-inline: max(var(--container-offset), calc((100% - var(--content-width)) / 2));

		@media (min-width: 768px) {
			padding-left: 3rem;
		}

		@media (min-width: 1200px) {
			padding-left: 5rem;
		}
	}

	&-info {
		width: 100%;
		color: var(--gray);
		padding-block: 3rem;
		
		@media (min-width: 768px) {
			max-width: 20rem;
			height: 100vh;
			height: 100svh;
			height: 100dvh;
			padding-block: var(--header-height) 2rem;
			position: sticky;
			top: 0;
			left: 0;
			z-index: 1020;
		}

		&-inner {

			@media (min-width: 768px) {
				width: 100%;
				height: 100%;

				overflow-y: auto;
				overflow-x: hidden;

				scrollbar-width: none;

				&::-webkit-scrollbar {
					display: none;
				}
			}
		}
	}

	&-header {
		margin-bottom: 5rem;

		@media (min-width: 992px) {
			margin-bottom: 6.25rem;
		}
	}

	&-title,
	&-price {
		font-size: 2rem;

		@media (min-width: 992px) {
			font-size: 2.5rem;
		}
	}

	&-title {
		letter-spacing: normal;
		text-transform: uppercase;
	}

	&-price {
		font-weight: 600;

		&-sale {
			color: var(--gray-250);
			text-decoration: line-through;
		}
	}

	&-sizes,
	&-colors {
		margin-bottom: 1rem;
		
		@media (min-width: 992px) {
			margin-bottom: 1.5rem;
		}
	}

	&-sizes {

		.btn-check {

			+ .btn {
				--_color: inherit;
			}

			&:checked {

				+ .btn {
					--_color: var(--gray-800)
				}
			}
		}
	}

	&-colors {
		display: flex;
		flex-wrap: wrap;
		gap: .25rem;

		@media (min-width: 992px) {
			gap: .5rem;
		}

		.btn-check {
			
			+ .btn {
				--_width: 1.5rem;
				--_height: 1.5rem;

				--_border-width: 2px;
				--_rounded: 50%;

				--_border-color-hover: var(--gray);
				--_border-color-active: var(--gray);

				@media (min-width: 992px) {
					--_width: 1.75rem;
					--_height: 1.75rem;
				}
			}

			&:checked {

				+ .btn {
					--_border-color: var(--gray);
				}
			}
		}
	}

	&-color {
		width: 100%;
		height: 100%;
		display: block;
		background-color: var(--_color);
		border-radius: 50%;
	}

	&-buttons {

		&:not(:first-child) {
			margin-top: 2.5rem;
		}
	}

	&-footer {
		margin-top: 4rem;
	}

	&-swiper {

		.swiper-wrapper {

			@media (min-width: 768px) {
				flex-direction: column;
			}
		}

		&-image {
			height: 70vh;
			display: block;
			
			@media (min-width: 768px) {
				height: 100vh;
				height: 100svh;
				height: 100dvh;
			}
		}
	}
}