.shop {
	padding-bottom: 0rem;

	@media (max-width: 992px) {
		height: 100%;
		min-height: 100%;
		overflow-y: auto;
		overflow-x: hidden;
	}

	.main-swiper-slide-container {

		@media (max-width: 992px) {
			height: auto;
		}
	}

	.section-header {
		margin-top: calc(var(--header-height) - var(--header-padding));

		@media (min-width: 992px) {
			margin-block: 5rem 2rem;
		}

		@media (min-width: 1200px) {
			margin-top: 4.75rem;
		}

		@media (min-width: 1400px) {
			margin-top: 4.25rem;
		}
	}

	.section-title {

		@media (max-width: 992px) {
			font-size: 5.75rem;
		}
	}

	.section-subtitle {
		font-size: .9375rem;
		font-weight: 600;

		@media (min-width: 1200px) {
			font-size: 1.25rem;
		}

		@media (min-width: 1400px) {
			font-size: 1.5rem;
		}
	}

	.swiper-filter {
		gap: .5rem;
		margin-block: -1rem;
		padding-block: 1rem;
		padding-inline: max(var(--container-offset), calc((100% - var(--content-width)) / 2));
		background-color: var(--body-bg);
		position: sticky;
		top: calc(var(--header-height, 6.25rem) - var(--header-padding));
		left: 0;
		z-index: 1020;
		
		@media (min-width: 992px) {
			gap: .75rem;
			position: static;
		}

		.btn-check {

			+ .btn {
				--_fs: .75rem;
				--_color: var(--gray-400);
				--_color-hover: var(--gray-400);
				--_color-active: var(--gray-400);

				@media (min-width: 992px) {
					--_fs: 1rem;
				}
			}

			&:checked {

				+ .btn {
					--_color: var(--gray-400);
					--_color-hover: var(--gray-400);
					--_color-active: var(--gray-400);

					text-shadow: 
						.025em 0 currentColor,
						0 .025em currentColor,
						-.025em 0 currentColor,
						0 -.025em currentColor;
				}
			}
		}

		+ .swiper {
			margin-top: 2rem;
			
			@media (min-width: 992px) {
				margin-top: 4rem;
			}
		}
	}

	&-swiper {
		flex: 1;
		display: flex;
		flex-direction: column;

		.swiper {
			width: 100%;
			flex: 1;

			&-wrapper {
				@media (max-width: 992px) {
					flex-direction: column;
					gap: .75rem;
				}
			}

			&-slide {

				> * {
					@media (max-width: 992px) {
						opacity: 1;
					}
				}
			}
		}
	}

	&-card {
		height: 100%;
		color: var(--light);
		position: relative;

		@media (max-width: 992px) {
			aspect-ratio: 3 / 4;
		}

		.stretched-link {

			@media (hover: hover) {
				&:hover {

					~ .shop-card-bg {

						&::after {
							opacity: 0;
						}
					}

					~ .shop-card-info {
						opacity: 1;
					}
				}
			}
		}

		&-bg::after,
		&-info {
			background-color: rgba(var(--gray-800-rgb), .5);
			position: absolute;
			transition-property: opacity;
			transition-duration: var(--transition-duration);
			transition-timing-function: var(--transition-timing-function);
		}

		&-bg {
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center;
			position: absolute;
			inset: 0;
			
			&::after {
				content: '';
				display: block;
				pointer-events: none;
				inset: 0;
				opacity: 0;

				@media (hover: hover) {
					opacity: 1;
				}
			}
		}


		&-info {
			display: flex;
			justify-content: space-between;
			padding: 1rem 1.5rem;
			bottom: 0;
			left: 0;
			right: 0;

			@media (min-width: 992px) {
				padding: 1.5rem 3rem;
			}

			@media (hover: hover) {
				opacity: 0;
			}

			&-end {
				display: flex;
				flex-direction: column;
				gap: .5rem;
				text-align: end;
			}
		}

		&-title {
			font-size: 1.25rem;
			letter-spacing: normal;
			
			@media (min-width: 992px) {
				font-size: 1.5rem;
			}
		}

		&-colors {
			display: flex;
			gap: .375rem;

			@media (min-width: 992px) {
				gap: .5rem;
			}
		}

		&-color {
			--_size: .75rem;
			--_color: #000;
			
			display: inline-block;
			width: var(--_size);
			height: var(--_size);
			background-color: var(--_color);
			border-radius: 50%;
			
			@media (min-width: 992px) {
				--_size: 1rem;
			}
		}
	}
}