//
// Base styles
//

.accordion {
  // scss-docs-start accordion-css-vars
  --bs-accordion-color: inherit;
  --bs-accordion-bg: var(--body-bg);
  --bs-accordion-border-color: var(--gray-300);
  --bs-accordion-border-width: 1px;
  --bs-accordion-border-radius: var(--rounded);
  --bs-accordion-inner-border-radius: var(--rounded);
  --bs-accordion-btn-padding-x: 0;
  --bs-accordion-btn-padding-y: .75rem;
  --bs-accordion-btn-fs: 1rem;
  --bs-accordion-btn-color: inherit;
  --bs-accordion-btn-bg: transparent;
  --bs-accordion-btn-icon-width: 1rem;
  --bs-accordion-btn-icon-transform: rotate(-180deg);
  --bs-accordion-btn-icon-transition: transform var(--transition-duration) var(--transition-timing-function);
  --bs-accordion-btn-focus-box-shadow: unset;
  --bs-accordion-body-padding-x: 0;
  --bs-accordion-body-padding-y: 1rem;
  --bs-accordion-active-color: var(--gray-800);
  --bs-accordion-active-bg: transparent;
  // scss-docs-end accordion-css-vars

  font-size: 1rem;
}

.accordion-button {
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
  width: 100%;
  font-size: var(--bs-accordion-btn-fs);
  color: var(--bs-accordion-btn-color);
  text-align: left; // Reset button style
  background-color: var(--bs-accordion-btn-bg);
  border: 0;
  border-radius: 0;
  padding: var(--bs-accordion-btn-padding-y) var(--bs-accordion-btn-padding-x);
  overflow-anchor: none;
  transition-property: color, border, box-shadow;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-timing-function);

  &:not(.collapsed) {
    color: var(--bs-accordion-active-color);
    background-color: var(--bs-accordion-active-bg);
    box-shadow: inset 0 calc(-1 * var(--bs-accordion-border-width)) 0 var(--bs-accordion-border-color);

    &::after {
      transform: rotate(0deg);
    }
  }

  &:hover {
    z-index: 2;
  }

  &::after,
  &::before {
    content: "";
    flex-shrink: 0;
    width: var(--bs-accordion-btn-icon-width);
    height: .0625rem;
    background-color: currentColor;
    position: absolute;
    top: 50%;
    right: 0;
  }

  &::after {
    transform: rotate(90deg);
    transition: var(--bs-accordion-btn-icon-transition);
  }
}

.accordion-header {
  margin-bottom: 0;
}

.accordion-item {
  color: var(--bs-accordion-color);
  background-color: var(--bs-accordion-bg);
  border: var(--bs-accordion-border-width) solid var(--bs-accordion-border-color);

  &:first-of-type {
    border-top-left-radius: var(--bs-accordion-border-radius);
    border-top-right-radius: var(--bs-accordion-border-radius);

    > .accordion-header .accordion-button {
      border-top-left-radius: var(--bs-accordion-border-radius);
      border-top-right-radius: var(--bs-accordion-border-radius);
    }
  }

  &:not(:first-of-type) {
    border-top: 0;
  }

  // Only set a border-radius on the last item if the accordion is collapsed
  &:last-of-type {
    border-bottom-left-radius: var(--bs-accordion-border-radius);
    border-bottom-right-radius: var(--bs-accordion-border-radius);

    > .accordion-header .accordion-button {
      &.collapsed {
        border-bottom-left-radius: var(--bs-accordion-border-radius);
        border-bottom-right-radius: var(--bs-accordion-border-radius);
      }
    }

    > .accordion-collapse {
      border-bottom-left-radius: var(--bs-accordion-border-radius);
      border-bottom-right-radius: var(--bs-accordion-border-radius);
    }
  }
}

.accordion-collapse {
  display: grid;
  grid-template-rows: 0fr;
  padding: 0 var(--bs-accordion-body-padding-x);
  transition-property: grid-template-rows, padding;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-timing-function);

  &.show {
    grid-template-rows: 1fr;
    padding: var(--bs-accordion-body-padding-y) var(--bs-accordion-body-padding-x);
  }
}

.accordion-body {
  overflow: hidden;
}


// Flush accordion items
//
// Remove borders and border-radius to keep accordion items edge-to-edge.

.accordion-flush {
  > .accordion-item {
    border-right: 0;
    border-left: 0;
    border-radius: 0;

    > .accordion-header .accordion-button {
      &,
      &.collapsed {
        border-radius: 0
      }
    }

    > .accordion-collapse {
      border-radius: 0;
    }
  }
}
