.header {
	color: var(--gray);
	padding-block: var(--header-padding);
	pointer-events: none;
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	z-index: 1030;
	transition-property: background;
	transition-duration: var(--transition-duration);
	transition-timing-function: var(--transition-timing-function);

	&-container {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		gap: 4rem;
		text-align: end;

		.btn {
			--_color: inherit;

			pointer-events: auto;
		}
	}

	&-top {
		width: 100%;
		flex-direction: column;
		
		&,
		&-main {
			display: flex;
			gap: 1rem;
		}

		&-main {
			align-items: center;
			justify-content: space-between;
		}
	}

	&-buttons {
		display: flex;
		align-items: center;
		gap: 1.5rem;
		margin-left: auto;
	}

	&-nav {
		text-align: end;

		+ .header-nav {
			margin-top: 2rem;
		}

		+ .contacts {
			margin-top: 4rem;
		}

		.nav__list {
			display: flex;
			flex-direction: column;
			gap: .5rem;

			.btn {
				--_color: inherit;

				pointer-events: auto;
			}
		}
	}

	.logo,
	.btn-back {
		display: none;
	}

	.btn-back {
		--_width: max-content;

		margin-left: auto;
	}

	&:not(.header--card, .header--shop) {
		.btn-cart {
			opacity: 0;
			pointer-events: none;
		}
	}

	&.is-sticky {
		background-color: var(--body-bg);
	}

	&--shop {

		@media (max-width: 992px) {
			pointer-events: auto;
			background-color: var(--body-bg);
		}
	}

	&--card,
	&--cart {

		.header-nav {
			display: none;
		}

		.logo {
			display: block;
		}

		.btn-back {
			display: inline-flex;
		}

		&.is-sticky {

			@media (min-width: 768px) {
				background-color: transparent;
			}
		}
	}

	&--card {

		.header-top {
			@media (min-width: 768px) {
				width: 50%;
				background-color: var(--body-bg);
				margin-left: auto;
				padding-left: 3rem;
			}
	
			@media (min-width: 1200px) {
				padding-left: 5rem;
			}
		}
	}
}