::-webkit-scrollbar {
	width: .125rem;
	height: .125rem;
}

::-webkit-scrollbar-track {
	background-color: rgba(var(--gray-250-rgb), .8);
	border-radius: .5rem;
}

::-webkit-scrollbar-thumb {
	background-color: rgba(var(--gray-rgb), .5);

	&:hover {
		background-color: var(--gray-800);
	}
}