.alert-page {
	color: var(--gray);
	text-align: center;
	margin-top: calc(var(--header-height) + 1.5rem);

	.hero-footer {
		position: absolute;
		right: max(var(--container-offset), calc((100% - var(--content-width)) / 2));
		bottom: var(--header-padding);
		z-index: 1;

		&,
		&-copy {
			text-align: right;
		}
	}

	&-content {
		max-width: 30rem;
		margin-inline: auto;

		@media (min-width: 992px) {
			max-width: 35rem;
		}
	}

	&-info {
		min-height: 5rem;
		margin-bottom: 2.5rem;

		@media (min-width: 992px) {
			margin-bottom: 3rem;
		}
	}

	&-title {
		font-weight: 300;
		letter-spacing: normal;
		text-transform: uppercase;
		margin-bottom: 2rem;

		@media (min-width: 992px) {
			margin-bottom: 2.5rem;
		}
	}

	&-text {
		margin-bottom: 1.5rem;

		@media (min-width: 992px) {
			margin-bottom: 2rem;
		}
	}

	&-btn {
		--_width: 100%;

		@media (min-width: 992px) {
			--_width: calc(100% - 3rem);

			margin-inline: 1.5rem;
		}
	}
}